input {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font-weight: normal;
  vertical-align: baseline;
  /*清楚点击阴影：*/
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  /*清楚按钮圆角：*/
  -webkit-appearance: none;
  border-radius: 0;
  /*选中文字设置：*/
