/* 水波纹 */
button {
  position: relative;
  overflow: hidden;
  border: none;
}
button:after {
  content: "";
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  pointer-events: none;
  background-image: radial-gradient(circle, #666 10%, transparent 10.01%);
  background-repeat: no-repeat;
  background-position: 50%;
  transform: scale(10, 10);
  opacity: 0;
  transition: transform .3s, opacity .5s;
}
button:active:after {
  transform: scale(0, 0);
  opacity: .3;
  transition: 0s;
}
.bg_cover {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}
.space_ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.space_line {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2;
  overflow: hidden;
}
